// from prosemirror-schema-basic
export var bold = {
    parseDOM: [
        {
            tag: 'b',
            // @ts-ignore
            getAttrs: function (element) {
                return element.style.fontWeight != 'normal' && null;
            },
        },
        { tag: 'strong' },
        {
            style: 'font-weight',
            // @ts-ignore
            getAttrs: function (style) {
                return /^(bold(er)?|[5-9]\d{2,})$/.test(style) && null;
            },
        },
    ],
    toDOM: function () { return ['b', 0]; },
};
